import { Component, OnInit } from '@angular/core';
import { UserService } from '../services/user.service';
import { ApiService } from '../services';

@Component({
  selector: 'app-producer',
  templateUrl: './patient.component.html',
  styleUrls: ['./patient.component.scss']
})
export class PatientComponent implements OnInit {
  
  currentUser: any;

  csvContent: string;
  data: Array<any> = [];
  properties:any = "";
  flag:boolean = false;
  submitted = false;
  success = false;

  constructor(private userService: UserService,private apiService: ApiService) { }

  ngOnInit() {
    this.currentUser = this.userService.getCurrentUser();
  }
//prepare data
  onFileLoad(fileLoadedEvent) {
    const textFromFileLoaded = fileLoadedEvent.target.result;
    this.csvContent = textFromFileLoaded;

    let flag = false;
    let objarray: Array<any> = [];
    let prop: Array<any> = [];
    let size: any = 0;
    let patientId: any = 1;
    let doctorId: any = 1;


    for (const line of this.csvContent.split(/[\r\n]+/)) {

      if (flag) {

        let obj = {};
        for (let k = 0; k < size; k++) {
          
          //Dynamic Object Properties
          obj[prop[k]] = line.split(',')[k]
         
        }
        if(obj["age"] == undefined || obj["cxr_label"] == undefined) {
         //filter out
        }else{
          obj["patientId"] = "pt"+patientId;
          obj["doctorId"] = "dt"+doctorId;
          obj["gender"] = Math.floor(Math.random() * Math.floor(2));
          obj["symptoms"]= { "days_since_symptom_onset":obj["days_since_symptom_onset"],
          "cough":obj["cough"],
          "cough_severity":obj["cough_severity"],
          "fever":obj["fever"],
          "sob":obj["sob"],
          "sob_severity":obj["sob_severity"],
          "diarrhea":obj["diarrhea"],
          "fatigue":obj["fatigue"],
          "headache":obj["headache"],
          "loss_of_smell":obj["loss_of_smell"],
          "loss_of_taste":obj["loss_of_taste"],
          "runny_nose":obj["runny_nose"],
          "muscle_sore":obj["muscle_sore"],
          "sore_throat":obj["sore_throat"],
        };
        for(let i in obj["symptoms"]){
         delete obj[i];
        }

          patientId++;
          if(patientId > doctorId+5){
            doctorId++;
          }

          //console.log(obj);
          objarray.push(obj);
        }
        

      } else {
        for (let k = 0; k < line.split(',').length; k++) {
          size = line.split(',').length;
          //Removing all the spaces to make them usefull
          prop.push(line.split(',')[k].replace(/ /g, ''));
        }
        flag = true;
      }
    }
    this.data = objarray;
    this.properties = [];
  
    //this.properties = prop;
    //console.log(this.properties);
    console.log(this.data);
    this.create();
    this.flag = true;
  

  }




  onFileSelect(input: HTMLInputElement) {

    const files = input.files;
    var fileTypes = ['csv'];  

    if (files && files.length) {
      var extension = input.files[0].name.split('.').pop().toLowerCase(), 
      isSuccess = fileTypes.indexOf(extension) > -1;  
      if(isSuccess){
        const fileToRead = files[0];

        const fileReader = new FileReader();
        fileReader.onload = this.onFileLoad;
  
        fileReader.readAsText(fileToRead, "UTF-8");
      }else{
        console.log("Error");
      }

    
    }

  }
  create(){
    
    console.log("in create");

    this.apiService.createEMR().subscribe(api => {
      this.data[0] = api
      //console.log(this.order);
      //this.apiService.queryOrders();
      this.success = true;
      //alert ("Order Created Successfully!")
    }, error => {
      this.success = false;
      alert("Problem creating Order: " + error['error']['message'])
    })
  }
  

}
